body {
  margin: 0 0 0 0;
}

.YearCategoryElement {
  display: flex;
  cursor: pointer;

}

.Year {
  margin: 3px 10px 3px 10px;
}

.Laureates {
  display: flex;
  margin: 20px 0 0 0;
}

.TableInfo {
  position: fixed;
  left: 200px;
}

.ListOfLaureates_header {
  padding-left: 200px;
  margin: 0 0 10px 0;
}

.firstname {

}

.surname {
  margin: 0 0 0 10px;
}

.motivation {
  margin: 0 0 0 10px;
}

.name {
  display: flex;
  font-size: 1.3em;
  font-weight: bold;
}

.laureat {
  display: flex;
  justify-content: space-between;
  margin: 5px 10px 5px 0;
  border-style: solid;
  border-color: #b1a1da;
  border-width: 1px;
  padding: 5px;
}

.header {
  position: fixed;
  top: 0px;
  background-color: #b1a1da;
  width: 100%;
  border-radius: 0 0 5px 5px;
  height: 6%;

}

.listOfYearCategoryElements {
  margin: 10% 0 0 0;
}
